<template>
  <div class="header">
  	<div>{{title}}</div>
  	<span @click=$router.back()><i v-if="show" class="fa fa-angle-left" :aria-hidden="true"></i></span>
  </div>
</template>

<script>
  export default{
  	name:"MyHeader",
  	props:{
  		title:{
  			type:String,
  			default:""
  		},
  		show:{
  			type:Boolean,
  			default:true
  		}
	}
}
</script>

<style scoped="scoped">
.header{
	height:56px;
	line-height:56px;
	position:relative;
	text-align:center;
	font-size:15px;
	background:#fd7f47;
	color:#fff;
}
.header span{
	height:100%;
	position:absolute;
	display:flex;
	align-items: center;
	left:0;
	top:0;
	margin-left:22px;
}
.fa-angle-left{
	font-size:25px;
}
</style>



