<template>
	<div class="container">
		<div>
			<span @click=$router.back()>{{backBtn}}</span>
			<span @click="next">{{nextBtn}}</span>
		</div>
	</div>
</template>
<script>
export default {
  name: 'FootButton',
  props:{
  	backBtn:{
  		default:"",
  		type:String
  	},
  	nextBtn:{
  		default:"",
  		type:String
  	}
  },
  methods:{
  	next(){
  		this.$emit("next")
  	}
  }
}
</script>
<style lang="stylus" scoped>
.container>div{
	background:#fff;
	display:flex;
	justify-content: space-around;
	padding:25px 0;
	&>span{
		padding:12px 48px;
		border:1px solid #fd7f47;
		color:#fd7f47;
		border-radius:5px;
	}
	&>span:nth-child(2){
		background:#fd7f47;
		color:#fff;
	}
}

</style>


