import Vue from 'vue';
import axios from 'axios';
import {Style,createAPI,Toast} from 'cube-ui';
Vue.use(Toast)
//axios
var instance = axios.create({
    baseURL:process.env.API,
    timeout: 90000
});
var toast=(type="loading",txt="loading...",mask=true,time=0)=>Toast.$create({
  type:type,
  txt:txt,
  mask: mask,
  time:time
})
// http
instance.interceptors.request.use(config => {
  if(!config.data.pageSize){
    toast().show()
  }
  return config
}, error => {
  setTimeout(() => {
     toast().hide();
     toast("txt","",false,2000).show()
  },3000)
  return Promise.reject(error)
})
// http
instance.interceptors.response.use(data => {// loading
  toast().hide();
  return data
}, error => {
  setTimeout(() => {
    toast().hide()
    toast("txt","",false,2000).show()
  },1000)
  return Promise.reject(error)
})
export default function (method, url, data = null) {
    method = method.toLowerCase();
    if (method == 'post') {
        return instance.post(url, data)
    } else if (method == 'get') {
        return instance.get(url, { params: data })
    } else if (method == 'delete') {
        return instance.delete(url, { params: data })
    }else if(method == 'put'){
        return instance.put(url,data)
    }else{
        console.error('method'+method)
        return false
    }
}



