import Vue from 'vue';
import {Style,createAPI,Toast} from 'cube-ui';
Vue.use(Toast)
//
export const IDREG=(that,val='',msg='')=>{
	const reg=/\d{17}[0-9Xx]|\d{15}/g;
	let flag=reg.test(val);
	if(!flag){
		const toast = that.$createToast({
	    	time: 2000,
	    	type:"txt",
	    	txt: "<div style='text-align:center;'>"+""+msg+"</div>"
	    })
	    toast.show();
	}
	return flag;
};
//
export const TELREG=(that,val='',msg='')=>{
	const reg=/^[1][3,4,5,6,7,8,9][0-9]{9}$/;
	let flag=reg.test(val);
	if(!flag){
		const toast = that.$createToast({
	    	time: 2000,
	    	type:"txt",
	    	txt: "<div style='text-align:center;'>"+"'13''14''15''16''17''18''19' 11"+"</div>"
	    })
	    toast.show();
	}
	return flag;
};
//input
export const ISINPUTNULL=(that,val='',msg='')=>{
	if(!val){
		const toast = that.$createToast({
	    	time: 2000,
	    	type:"txt",
	    	txt: "<div style='text-align:center;'>"+msg+""+"</div>"
	    })
	    toast.show()
	}
	return !!val;
}


